"use client";

import { useState, useEffect, Fragment } from "react";
import Image from "next/image";
import { toast } from "react-toastify";
import { adminService } from "../../services/adminService";
import { 
  Users, 
  Download, 
  Search, 
  ChevronLeft, 
  ChevronRight,
  Eye,
  Loader2,
} from "lucide-react";
import HoroscopeChart from "@/app/components/horoscope/HoroscopeChart";
import DashaInfromationCard from "@/app/components/horoscope/DashaInfromationCard";
import AstroDataCompunent from "@/app/components/horoscope/astroDataCompunent";

interface User {
  id: number;
  name: string;
  email: string;
  username: string;
  nikname: string;
  dateOfBirth: string;
  birthTime: string;
  WhatsappNumber: string;
  reference: string | null;
  gender: { id: number; type: string };
  birthLocation: { id: number; name: string };
  createdAt: string;
  updatedAt: string;
  subUsers?: User[];
}

interface CompleteUserData {
  success: boolean;
  data: {
    personalInfo: any;
    profileImages: any[];
    astrologicalData: any;
    predictions: any[];
    employment: any[];
    education: any[];
  };
}

interface TransformedChart {
  lagnaya: string;
  box1: string[];
  box2: string[];
  box3: string[];
  box4: string[];
  box5: string[];
  box6: string[];
  box7: string[];
  box8: string[];
  box9: string[];
  box10: string[];
  box11: string[];
  box12: string[];
}

const transformChart = (rawData: any): TransformedChart | null => {
  if (!rawData) return null;
  const toArray = (box: string | null) =>
    box ? box.split(",").map((item) => item.trim()) : [];
  return {
    lagnaya: rawData.lagnaya,
    box1: toArray(rawData.box1),
    box2: toArray(rawData.box2),
    box3: toArray(rawData.box3),
    box4: toArray(rawData.box4),
    box5: toArray(rawData.box5),
    box6: toArray(rawData.box6),
    box7: toArray(rawData.box7),
    box8: toArray(rawData.box8),
    box9: toArray(rawData.box9),
    box10: toArray(rawData.box10),
    box11: toArray(rawData.box11),
    box12: toArray(rawData.box12),
  };
};

export default function UserManagement() {
  const [users, setUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(false);
  const [exporting, setExporting] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [totalUsers, setTotalUsers] = useState(0);
  const [selectedUser, setSelectedUser] = useState<CompleteUserData | null>(null);
  const [expandedUsers, setExpandedUsers] = useState<Set<number>>(new Set());
  const limit = 10;

  useEffect(() => {
    fetchUsers();
  }, [currentPage]);

  const fetchUsers = async () => {
    try {
      setLoading(true);
      const result = await adminService.getAllUsers(currentPage, limit, searchTerm);
      
      if (result.success) {
        setUsers(result.data.users);
        setTotalUsers(result.data.pagination.total);
        setTotalPages(result.data.pagination.totalPages);
      }
    } catch (error: any) {
      toast.error("Failed to fetch users");
    } finally {
      setLoading(false);
    }
  };

  const handleSearch = () => {
    setCurrentPage(1);
    fetchUsers();
  };

  const handleExport = async () => {
    try {
      setExporting(true);
      await adminService.exportAllData();
      toast.success("Data exported successfully!");
    } catch (error) {
      toast.error("Failed to export data");
    } finally {
      setExporting(false);
    }
  };

  const viewUserDetails = async (userId: number) => {
    try {
      const result: any = await adminService.getUserCompleteData(userId.toString());
      if (result.success) {
        setSelectedUser(result);
      } else {
        toast.error("Failed to load complete user data");
      }
    } catch (error) {
      toast.error("Failed to fetch user details");
    }
  };

  const toggleUserExpansion = (userId: number) => {
    const newExpanded = new Set(expandedUsers);
    if (newExpanded.has(userId)) {
      newExpanded.delete(userId);
    } else {
      newExpanded.add(userId);
    }
    setExpandedUsers(newExpanded);
  };

  return (
    <div className="space-y-6">
      {/* Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-gray-600">Total Users</p>
              <p className="text-3xl font-bold text-gray-900">{totalUsers}</p>
            </div>
            <Users className="w-12 h-12 text-indigo-600" />
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-gray-600">Current Page</p>
              <p className="text-3xl font-bold text-gray-900">{currentPage}</p>
            </div>
            <div className="w-12 h-12 bg-indigo-100 rounded-full flex items-center justify-center">
              <span className="text-indigo-600 font-bold">{totalPages}</span>
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <button
            onClick={handleExport}
            disabled={exporting}
            className="w-full flex items-center justify-center space-x-2 px-4 py-3 bg-green-600 text-white rounded-lg hover:bg-green-700 transition disabled:opacity-50"
          >
            {exporting ? (
              <>
                <Loader2 className="w-5 h-5 animate-spin" />
                <span>Exporting...</span>
              </>
            ) : (
              <>
                <Download className="w-5 h-5" />
                <span>Export All Data</span>
              </>
            )}
          </button>
        </div>
      </div>

      {/* Search Bar */}
      <div className="bg-white rounded-lg shadow p-4">
        <div className="flex items-center space-x-4">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
            <input
              type="text"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
              placeholder="Search by name, email, or username..."
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
            />
          </div>
          <button
            onClick={handleSearch}
            className="px-6 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition"
          >
            Search
          </button>
        </div>
      </div>

      {/* Users Table */}
      <div className="bg-white rounded-lg shadow overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-gray-50 border-b">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">ID</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Name</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Email</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Username</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Gender</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Sub Users</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {loading ? (
                <tr>
                  <td colSpan={7} className="px-6 py-12 text-center">
                    <Loader2 className="w-8 h-8 animate-spin text-indigo-600 mx-auto" />
                  </td>
                </tr>
              ) : users.length === 0 ? (
                <tr>
                  <td colSpan={7} className="px-6 py-12 text-center text-gray-500">
                    No users found
                  </td>
                </tr>
              ) : (
                users.map((user: any) => (
                  <Fragment key={user.id}>
                    <tr className="hover:bg-gray-50">
                      <td className="px-6 py-4 text-sm text-gray-900">{user.id}</td>
                      <td className="px-6 py-4 text-sm text-gray-900">
                        <div className="flex items-center space-x-2">
                          <span>{user.name}</span>
                          {user.subUsers?.length > 0 && (
                            <button
                              onClick={() => toggleUserExpansion(user.id)}
                              className="text-indigo-600 hover:text-indigo-900"
                            >
                              <svg
                                className={`w-4 h-4 transition-transform ${expandedUsers.has(user.id) ? 'rotate-180' : ''}`}
                                fill="none"
                                stroke="currentColor"
                                viewBox="0 0 24 24"
                              >
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                              </svg>
                            </button>
                          )}
                        </div>
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">{user.email}</td>
                      <td className="px-6 py-4 text-sm text-gray-600">{user.username}</td>
                      <td className="px-6 py-4 text-sm text-gray-600">{user.gender?.type || '-'}</td>
                      <td className="px-6 py-4 text-sm text-gray-600">
                        {user.subUsers?.length || 0}
                      </td>
                      <td className="px-6 py-4">
                        <button
                          onClick={() => viewUserDetails(user.id)}
                          className="text-indigo-600 hover:text-indigo-900"
                        >
                          <Eye className="w-5 h-5" />
                        </button>
                      </td>
                    </tr>
                    {expandedUsers.has(user.id) && user.subUsers?.map((subUser: any) => (
                      <tr key={`sub-${subUser.id}`} className="bg-gray-50 hover:bg-gray-100">
                        <td className="px-6 py-4 text-sm text-gray-900 pl-12">↳ {subUser.id}</td>
                        <td className="px-6 py-4 text-sm text-gray-900">{subUser.name}</td>
                        <td className="px-6 py-4 text-sm text-gray-600">{subUser.email}</td>
                        <td className="px-6 py-4 text-sm text-gray-600">{subUser.username}</td>
                        <td className="px-6 py-4 text-sm text-gray-600">{subUser.gender?.type || '-'}</td>
                        <td className="px-6 py-4 text-sm text-gray-600">0</td>
                        <td className="px-6 py-4">
                          <button
                            onClick={() => viewUserDetails(subUser.id)}
                            className="text-indigo-600 hover:text-indigo-900"
                          >
                            <Eye className="w-5 h-5" />
                          </button>
                        </td>
                      </tr>
                    ))}
                  </Fragment>
                ))
              )}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        {totalPages > 1 && (
          <div className="bg-gray-50 px-6 py-4 flex items-center justify-between border-t">
            <div className="text-sm text-gray-700">
              Page <span className="font-medium">{currentPage}</span> of{' '}
              <span className="font-medium">{totalPages}</span>
            </div>
            <div className="flex space-x-2">
              <button
                onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                disabled={currentPage === 1}
                className="px-3 py-2 border rounded-lg hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <ChevronLeft className="w-5 h-5" />
              </button>
              <button
                onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                disabled={currentPage === totalPages}
                className="px-3 py-2 border rounded-lg hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                <ChevronRight className="w-5 h-5" />
              </button>
            </div>
          </div>
        )}
      </div>

      {/* User Details Modal - (keeping your existing modal code) */}
      {selectedUser && (
          <div className="fixed inset-0 bg-gray-900 bg-opacity-75 z-50 overflow-y-auto flex items-start justify-center pt-0">
            <div className="bg-white w-full min-h-screen relative max-w-none mx-0">
              <div className="sticky top-0 bg-white px-6 py-4 border-b flex items-center justify-between z-10">
                <h2 className="text-2xl font-bold text-gray-900">
                  User Details: {selectedUser.data.personalInfo?.name || 'Unknown'}
                </h2>
                <button
                  onClick={() => setSelectedUser(null)}
                  className="text-gray-500 hover:text-gray-700 transition-colors"
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
              <div className="p-6">
                {/* Personal Info */}
                {selectedUser.data.personalInfo && (
                  <section className="mb-8">
                    <h3 className="text-xl font-semibold text-gray-900 mb-4 border-b pb-2">Personal Information</h3>
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                      <div><span className="font-medium text-gray-700">Name:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.name || 'N/A'}</span></div>
                      <div><span className="font-medium text-gray-700">Email:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.email || 'N/A'}</span></div>
                      <div><span className="font-medium text-gray-700">Username:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.username || 'N/A'}</span></div>
                      <div><span className="font-medium text-gray-700">Nickname:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.nikname || 'N/A'}</span></div>
                      <div><span className="font-medium text-gray-700">Date of Birth:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.dateOfBirth || 'N/A'}</span></div>
                      <div><span className="font-medium text-gray-700">Birth Time:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.birthTime || 'N/A'}</span></div>
                      <div><span className="font-medium text-gray-700">WhatsApp:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.WhatsappNumber || 'N/A'}</span></div>
                      <div><span className="font-medium text-gray-700">Gender:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.gender?.type || 'N/A'}</span></div>
                      <div><span className="font-medium text-gray-700">Birth Location:</span> <span className="text-gray-900">{selectedUser.data.personalInfo.birthLocation?.name || 'N/A'}</span></div>
                    </div>
                  </section>
                )}

                {/* Profile Images */}
                {selectedUser.data.profileImages?.length > 0 && (
                  <section className="mb-8">
                    <h3 className="text-xl font-semibold text-gray-900 mb-4 border-b pb-2">Profile Images</h3>
                    <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                      {selectedUser.data.profileImages.map((img) => (
                        <Image
                          key={img.id}
                          src={img.imagePath}
                          alt="Profile"
                          width={200}
                          height={200}
                          className="rounded-lg shadow"
                        />
                      ))}
                    </div>
                  </section>
                )}

                {/* Astrological Data */}
                {selectedUser.data.astrologicalData && (
                  <section className="mb-8">
                    <h3 className="text-xl font-semibold text-gray-900 mb-4 border-b pb-2">Astrological Data</h3>
                    <div className="space-y-6">
                      {selectedUser.data.astrologicalData.planetHouse && (
                        <HoroscopeChart
                          data={transformChart(selectedUser.data.astrologicalData.planetHouse)}
                          lagnaya={selectedUser.data.astrologicalData.planetHouse.lagnaya}
                          isHoroscope={true}
                        />
                      )}
                      {selectedUser.data.astrologicalData.navamsaka && (
                        <HoroscopeChart
                          data={transformChart(selectedUser.data.astrologicalData.navamsaka)}
                          lagnaya={selectedUser.data.astrologicalData.navamsaka.lagnaya}
                          isHoroscope={false}
                        />
                      )}
                      {selectedUser.data.astrologicalData.current_mahadasha_set && (
                        <DashaInfromationCard
                          items={selectedUser.data.astrologicalData.current_mahadasha_set}
                          ismahaDaha={true}
                        />
                      )}
                      {selectedUser.data.astrologicalData.current_antardasha_set && (
                        <DashaInfromationCard
                          items={selectedUser.data.astrologicalData.current_antardasha_set}
                          ismahaDaha={false}
                        />
                      )}
                      {selectedUser.data.astrologicalData.astrologicalDetails && (
                        <AstroDataCompunent
                          astroData={selectedUser.data.astrologicalData.astrologicalDetails}
                        />
                      )}
                      {!selectedUser.data.astrologicalData.planetHouse && !selectedUser.data.astrologicalData.navamsaka && !selectedUser.data.astrologicalData.current_mahadasha_set && !selectedUser.data.astrologicalData.current_antardasha_set && !selectedUser.data.astrologicalData.astrologicalDetails && (
                        <p className="text-gray-500">No astrological data available.</p>
                      )}
                    </div>
                  </section>
                )}

                {/* Predictions */}
                {selectedUser.data.predictions?.length > 0 && (
                  <section className="mb-8">
                    <h3 className="text-xl font-semibold text-gray-900 mb-4 border-b pb-2">Predictions</h3>
                    <div className="overflow-x-auto">
                      <table className="w-full bg-white border border-gray-200 rounded-lg">
                        <thead className="bg-gray-50">
                          <tr>
                            <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700">Planet</th>
                            <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700">General Prediction</th>
                            <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700">Personalised Prediction</th>
                            <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700">Planet Zodiac Prediction</th>
                            <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700">Verbal Location</th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-200">
                          {selectedUser.data.predictions.map((pred) => (
                            <tr key={pred.id} className="hover:bg-gray-50">
                              <td className="px-4 py-3 text-sm text-gray-900">{pred.planet?.PlanetName || 'N/A'}</td>
                              <td className="px-4 py-3 text-sm text-gray-700">{pred.general_prediction || 'N/A'}</td>
                              <td className="px-4 py-3 text-sm text-gray-700">{pred.personalised_prediction || 'N/A'}</td>
                              <td className="px-4 py-3 text-sm text-gray-700">{pred.planet_zodiac_prediction || 'N/A'}</td>
                              <td className="px-4 py-3 text-sm text-gray-700">{pred.verbal_location || 'N/A'}</td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </section>
                )}

                {/* Employment */}
                {selectedUser.data.employment?.length > 0 && (
                  <section className="mb-8">
                    <h3 className="text-xl font-semibold text-gray-900 mb-4 border-b pb-2">Employment</h3>
                    <ul className="space-y-2">
                      {selectedUser.data.employment.map((job) => (
                        <li key={job.id} className="text-gray-700">
                          • {job.jobName || 'N/A'} ({job.category || 'N/A'})
                        </li>
                      ))}
                    </ul>
                  </section>
                )}

                {/* Education */}
                {selectedUser.data.education?.length > 0 && (
                  <section className="mb-8">
                    <h3 className="text-xl font-semibold text-gray-900 mb-4 border-b pb-2">Education</h3>
                    <ul className="space-y-2">
                      {selectedUser.data.education.map((edu) => (
                        <li key={edu.id} className="text-gray-700">
                          • {edu.qualificationName || 'N/A'} ({edu.category || 'N/A'})
                        </li>
                      ))}
                    </ul>
                  </section>
                )}
              </div>
              <div className="sticky bottom-0 bg-white border-t border-gray-200 px-6 py-4 flex justify-end">
                <button
                  onClick={() => setSelectedUser(null)}
                  className="px-6 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        )}
    </div>
  );
}