"use client";

import { useState, useEffect } from "react";
import { toast } from "react-toastify";
import { adminService } from "../../services/adminService";
import {
  Plus,
  Edit,
  Trash2,
  Search,
  Loader2,
  X,
  Save,
  Filter,
} from "lucide-react";

interface PlanetPrediction {
  id: number;
  lagnaya: string;
  PredictionPlanetId: number;
  planetlocation: string;
  prediction: string;
  predictionPlanet?: {
    id: number;
    PlanetName: string;
  };
}

interface FormData {
  lagnaya: string;
  PredictionPlanetId: number | string;
  planetlocation: string;
  prediction: string;
}

export default function PlanetPredictionsManagement() {
  const [predictions, setPredictions] = useState<PlanetPrediction[]>([]);
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [editMode, setEditMode] = useState(false);
  const [selectedId, setSelectedId] = useState<number | null>(null);
  
  // Filters
  const [filterLagnaya, setFilterLagnaya] = useState("");
  const [filterPlanetId, setFilterPlanetId] = useState("");
  const [filterLocation, setFilterLocation] = useState("");

  // Form data
  const [formData, setFormData] = useState<FormData>({
    lagnaya: "",
    PredictionPlanetId: "",
    planetlocation: "",
    prediction: "",
  });

  // Predefined options
  const lagnayas = [
    "Mesha", "Vrushabha", "Mithuna", "Kataka", "Singha", "Kanya",
    "Thula", "Vrushchika", "Dhanu", "Makara", "Kumbha", "Meena"
  ];

  const planets = [
    { id: 1, name: "Sun" },
    { id: 2, name: "Moon" },
    { id: 3, name: "Mars" },
    { id: 4, name: "Mercury" },
    { id: 5, name: "Jupiter" },
    { id: 6, name: "Venus" },
    { id: 7, name: "Saturn" },
    { id: 8, name: "Rahu" },
    { id: 9, name: "Ketu" },
  ];

  const locations = Array.from({ length: 12 }, (_, i) => (i + 1).toString());

  useEffect(() => {
    fetchPredictions();
  }, []);

  const fetchPredictions = async () => {
    try {
      setLoading(true);
      const filters: any = {};
      if (filterLagnaya) filters.lagnaya = filterLagnaya;
      if (filterPlanetId) filters.planetId = parseInt(filterPlanetId);
      if (filterLocation) filters.planetlocation = filterLocation;

      const result = await adminService.getAllPlanetPredictions(filters);
      if (result.success) {
        setPredictions(result.data.data || []);
      } else {
        toast.error("Failed to fetch predictions");
      }
    } catch (error) {
      toast.error("Error fetching predictions");
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.lagnaya || !formData.PredictionPlanetId || !formData.planetlocation || !formData.prediction) {
      toast.error("All fields are required");
      return;
    }

    try {
      setLoading(true);
      const submitData = {
        ...formData,
        PredictionPlanetId: typeof formData.PredictionPlanetId === 'string' 
          ? parseInt(formData.PredictionPlanetId) 
          : formData.PredictionPlanetId,
      };

      let result;
      if (editMode && selectedId) {
        result = await adminService.updatePlanetPrediction(selectedId, submitData);
        toast.success("Prediction updated successfully!");
      } else {
        result = await adminService.createPlanetPrediction(submitData);
        toast.success("Prediction created successfully!");
      }

      if (result.success) {
        setShowModal(false);
        resetForm();
        fetchPredictions();
      } else {
        toast.error(result.error || "Operation failed");
      }
    } catch (error: any) {
      toast.error(error.message || "Operation failed");
    } finally {
      setLoading(false);
    }
  };

  const handleEdit = (prediction: PlanetPrediction) => {
    setEditMode(true);
    setSelectedId(prediction.id);
    setFormData({
      lagnaya: prediction.lagnaya,
      PredictionPlanetId: prediction.PredictionPlanetId,
      planetlocation: prediction.planetlocation,
      prediction: prediction.prediction,
    });
    setShowModal(true);
  };

  const handleDelete = async (id: number) => {
    if (!confirm("Are you sure you want to delete this prediction?")) return;

    try {
      setLoading(true);
      const result = await adminService.deletePlanetPrediction(id);
      if (result.success) {
        toast.success("Prediction deleted successfully!");
        fetchPredictions();
      } else {
        toast.error(result.error || "Delete failed");
      }
    } catch (error: any) {
      toast.error(error.message || "Delete failed");
    } finally {
      setLoading(false);
    }
  };

  const resetForm = () => {
    setFormData({
      lagnaya: "",
      PredictionPlanetId: "",
      planetlocation: "",
      prediction: "",
    });
    setEditMode(false);
    setSelectedId(null);
  };

  const openCreateModal = () => {
    resetForm();
    setShowModal(true);
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-gray-900">Planet Predictions</h2>
          <p className="text-sm text-gray-600 mt-1">
            Manage predictions for different planets, lagnayas, and locations
          </p>
        </div>
        <button
          onClick={openCreateModal}
          className="flex items-center space-x-2 px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition"
        >
          <Plus className="w-5 h-5" />
          <span>Add Prediction</span>
        </button>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-lg shadow p-4">
        <div className="flex items-center space-x-2 mb-4">
          <Filter className="w-5 h-5 text-gray-500" />
          <h3 className="text-lg font-semibold text-gray-900">Filters</h3>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <select
            value={filterLagnaya}
            onChange={(e) => setFilterLagnaya(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
          >
            <option value="">All Lagnayas</option>
            {lagnayas.map((lagnaya) => (
              <option key={lagnaya} value={lagnaya}>{lagnaya}</option>
            ))}
          </select>

          <select
            value={filterPlanetId}
            onChange={(e) => setFilterPlanetId(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
          >
            <option value="">All Planets</option>
            {planets.map((planet) => (
              <option key={planet.id} value={planet.id}>{planet.name}</option>
            ))}
          </select>

          <select
            value={filterLocation}
            onChange={(e) => setFilterLocation(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
          >
            <option value="">All Locations</option>
            {locations.map((loc) => (
              <option key={loc} value={loc}>Location {loc}</option>
            ))}
          </select>

          <button
            onClick={fetchPredictions}
            className="px-6 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition flex items-center justify-center space-x-2"
          >
            <Search className="w-4 h-4" />
            <span>Apply Filters</span>
          </button>
        </div>
      </div>

      {/* Predictions Table */}
      <div className="bg-white rounded-lg shadow overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-gray-50 border-b">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">ID</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Lagnaya</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Planet</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Location</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Prediction</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {loading ? (
                <tr>
                  <td colSpan={6} className="px-6 py-12 text-center">
                    <Loader2 className="w-8 h-8 animate-spin text-indigo-600 mx-auto" />
                  </td>
                </tr>
              ) : predictions.length === 0 ? (
                <tr>
                  <td colSpan={6} className="px-6 py-12 text-center text-gray-500">
                    No predictions found
                  </td>
                </tr>
              ) : (
                predictions.map((pred) => (
                  <tr key={pred.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 text-sm text-gray-900">{pred.id}</td>
                    <td className="px-6 py-4 text-sm text-gray-900">{pred.lagnaya}</td>
                    <td className="px-6 py-4 text-sm text-gray-900">
                      {pred.predictionPlanet?.PlanetName || `Planet ${pred.PredictionPlanetId}`}
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900">{pred.planetlocation}</td>
                    <td className="px-6 py-4 text-sm text-gray-600 max-w-xs truncate">
                      {pred.prediction}
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-center space-x-3">
                        <button
                          onClick={() => handleEdit(pred)}
                          className="text-indigo-600 hover:text-indigo-900"
                        >
                          <Edit className="w-5 h-5" />
                        </button>
                        <button
                          onClick={() => handleDelete(pred.id)}
                          className="text-red-600 hover:text-red-900"
                        >
                          <Trash2 className="w-5 h-5" />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Create/Edit Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-gray-900 bg-opacity-75 z-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="sticky top-0 bg-white px-6 py-4 border-b flex items-center justify-between">
              <h3 className="text-xl font-bold text-gray-900">
                {editMode ? "Edit Prediction" : "Create New Prediction"}
              </h3>
              <button
                onClick={() => setShowModal(false)}
                className="text-gray-500 hover:text-gray-700"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <form onSubmit={handleSubmit} className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Lagnaya *
                </label>
                <select
                  value={formData.lagnaya}
                  onChange={(e) => setFormData({ ...formData, lagnaya: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
                  required
                >
                  <option value="">Select Lagnaya</option>
                  {lagnayas.map((lagnaya) => (
                    <option key={lagnaya} value={lagnaya}>{lagnaya}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Planet *
                </label>
                <select
                  value={formData.PredictionPlanetId}
                  onChange={(e) => setFormData({ ...formData, PredictionPlanetId: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
                  required
                >
                  <option value="">Select Planet</option>
                  {planets.map((planet) => (
                    <option key={planet.id} value={planet.id}>{planet.name}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Planet Location *
                </label>
                <select
                  value={formData.planetlocation}
                  onChange={(e) => setFormData({ ...formData, planetlocation: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
                  required
                >
                  <option value="">Select Location</option>
                  {locations.map((loc) => (
                    <option key={loc} value={loc}>Location {loc}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Prediction Text *
                </label>
                <textarea
                  value={formData.prediction}
                  onChange={(e) => setFormData({ ...formData, prediction: e.target.value })}
                  rows={6}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
                  placeholder="Enter detailed prediction..."
                  required
                />
              </div>

              <div className="flex items-center justify-end space-x-3 pt-4">
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  className="px-6 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  disabled={loading}
                  className="flex items-center space-x-2 px-6 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition disabled:opacity-50"
                >
                  {loading ? (
                    <>
                      <Loader2 className="w-5 h-5 animate-spin" />
                      <span>Saving...</span>
                    </>
                  ) : (
                    <>
                      <Save className="w-5 h-5" />
                      <span>{editMode ? "Update" : "Create"}</span>
                    </>
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}