"use client";

import { useState } from "react";
import { toast } from "react-toastify";
import { adminService } from "../../services/adminService";
import {
  Plus,
  Edit,
  Eye,
  EyeOff,
  Save,
  Loader2,
  X,
  Key,
  Copy,
  Check,
} from "lucide-react";

interface ApiKeyData {
  id: string;
  key: string;
}

export default function ApiKeysManagement() {
  const [loading, setLoading] = useState(false);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showUpdateModal, setShowUpdateModal] = useState(false);
  const [showViewModal, setShowViewModal] = useState(false);
  
  // Form states
  const [newApiKey, setNewApiKey] = useState("");
  const [updateKeyId, setUpdateKeyId] = useState("");
  const [updateKeyValue, setUpdateKeyValue] = useState("");
  const [viewKeyId, setViewKeyId] = useState("");
  const [viewedKey, setViewedKey] = useState<ApiKeyData | null>(null);
  
  // UI states
  const [showFullKey, setShowFullKey] = useState(false);
  const [copied, setCopied] = useState(false);

  // Create API Key
  const handleCreate = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!newApiKey.trim()) {
      toast.error("API key is required");
      return;
    }

    try {
      setLoading(true);
      const result = await adminService.createApiKey(newApiKey);
      
      if (result.success) {
        toast.success("API key created successfully!");
        setShowCreateModal(false);
        setNewApiKey("");
      } else {
        toast.error(result.error || "Failed to create API key");
      }
    } catch (error: any) {
      toast.error(error.message || "Failed to create API key");
    } finally {
      setLoading(false);
    }
  };

  // Update API Key
  const handleUpdate = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!updateKeyId.trim() || !updateKeyValue.trim()) {
      toast.error("Both API key ID and new key value are required");
      return;
    }

    try {
      setLoading(true);
      const result = await adminService.updateApiKey(updateKeyValue, updateKeyId);
      
      if (result.success) {
        toast.success("API key updated successfully!");
        setShowUpdateModal(false);
        setUpdateKeyId("");
        setUpdateKeyValue("");
      } else {
        toast.error(result.error || "Failed to update API key");
      }
    } catch (error: any) {
      toast.error(error.message || "Failed to update API key");
    } finally {
      setLoading(false);
    }
  };

  // View API Key
  const handleView = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!viewKeyId.trim()) {
      toast.error("API key ID is required");
      return;
    }

    try {
      setLoading(true);
      const result = await adminService.getApiKey(viewKeyId);
      
      if (result.success && result.data.ApiKey) {
        setViewedKey(result.data.ApiKey);
        setShowViewModal(true);
        setViewKeyId("");
      } else {
        toast.error(result.error || "API key not found");
      }
    } catch (error: any) {
      toast.error(error.message || "Failed to fetch API key");
    } finally {
      setLoading(false);
    }
  };

  // Copy to clipboard
  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text);
    setCopied(true);
    toast.success("Copied to clipboard!");
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-gray-900">API Keys Management</h2>
          <p className="text-sm text-gray-600 mt-1">
            Manage API keys for external integrations
          </p>
        </div>
      </div>

      {/* Action Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* Create Card */}
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="p-3 bg-green-100 rounded-lg">
                <Plus className="w-6 h-6 text-green-600" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-900">Create API Key</h3>
                <p className="text-xs text-gray-600">Add new API key</p>
              </div>
            </div>
          </div>
          <button
            onClick={() => setShowCreateModal(true)}
            className="w-full px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition"
          >
            Create New Key
          </button>
        </div>

        {/* View Card */}
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="p-3 bg-blue-100 rounded-lg">
                <Eye className="w-6 h-6 text-blue-600" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-900">View API Key</h3>
                <p className="text-xs text-gray-600">View masked key</p>
              </div>
            </div>
          </div>
          <form onSubmit={handleView} className="space-y-2">
            <input
              type="text"
              value={viewKeyId}
              onChange={(e) => setViewKeyId(e.target.value)}
              placeholder="Enter API Key ID"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
            />
            <button
              type="submit"
              disabled={loading}
              className="w-full px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition disabled:opacity-50"
            >
              {loading ? <Loader2 className="w-5 h-5 animate-spin mx-auto" /> : "View Key"}
            </button>
          </form>
        </div>

        {/* Update Card */}
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="p-3 bg-indigo-100 rounded-lg">
                <Edit className="w-6 h-6 text-indigo-600" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-900">Update API Key</h3>
                <p className="text-xs text-gray-600">Modify existing key</p>
              </div>
            </div>
          </div>
          <button
            onClick={() => setShowUpdateModal(true)}
            className="w-full px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition"
          >
            Update Key
          </button>
        </div>
      </div>

      {/* Info Card */}
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <div className="flex items-start space-x-3">
          <Key className="w-5 h-5 text-blue-600 mt-0.5" />
          <div>
            <h4 className="font-semibold text-blue-900">About API Keys</h4>
            <p className="text-sm text-blue-700 mt-1">
              API keys are used for authenticating external services. When viewing a key, 
              you'll see a masked version (first 8 characters + ... + last 4 characters) 
              for security purposes. Only admins can create and update keys.
            </p>
          </div>
        </div>
      </div>

      {/* Create Modal */}
      {showCreateModal && (
        <div className="fixed inset-0 bg-gray-900 bg-opacity-75 z-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-md w-full">
            <div className="px-6 py-4 border-b flex items-center justify-between">
              <h3 className="text-xl font-bold text-gray-900">Create New API Key</h3>
              <button
                onClick={() => setShowCreateModal(false)}
                className="text-gray-500 hover:text-gray-700"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <form onSubmit={handleCreate} className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  API Key *
                </label>
                <input
                  type="text"
                  value={newApiKey}
                  onChange={(e) => setNewApiKey(e.target.value)}
                  placeholder="sk-1234567890abcdef..."
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500"
                  required
                />
                <p className="text-xs text-gray-500 mt-1">
                  Enter the full API key value
                </p>
              </div>

              <div className="flex items-center justify-end space-x-3 pt-4">
                <button
                  type="button"
                  onClick={() => setShowCreateModal(false)}
                  className="px-6 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  disabled={loading}
                  className="flex items-center space-x-2 px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition disabled:opacity-50"
                >
                  {loading ? (
                    <Loader2 className="w-5 h-5 animate-spin" />
                  ) : (
                    <>
                      <Save className="w-5 h-5" />
                      <span>Create</span>
                    </>
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Update Modal */}
      {showUpdateModal && (
        <div className="fixed inset-0 bg-gray-900 bg-opacity-75 z-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-md w-full">
            <div className="px-6 py-4 border-b flex items-center justify-between">
              <h3 className="text-xl font-bold text-gray-900">Update API Key</h3>
              <button
                onClick={() => setShowUpdateModal(false)}
                className="text-gray-500 hover:text-gray-700"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <form onSubmit={handleUpdate} className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  API Key ID *
                </label>
                <input
                  type="text"
                  value={updateKeyId}
                  onChange={(e) => setUpdateKeyId(e.target.value)}
                  placeholder="1"
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
                  required
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  New API Key Value *
                </label>
                <input
                  type="text"
                  value={updateKeyValue}
                  onChange={(e) => setUpdateKeyValue(e.target.value)}
                  placeholder="sk-newkey1234567890..."
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
                  required
                />
              </div>

              <div className="flex items-center justify-end space-x-3 pt-4">
                <button
                  type="button"
                  onClick={() => setShowUpdateModal(false)}
                  className="px-6 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  disabled={loading}
                  className="flex items-center space-x-2 px-6 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition disabled:opacity-50"
                >
                  {loading ? (
                    <Loader2 className="w-5 h-5 animate-spin" />
                  ) : (
                    <>
                      <Save className="w-5 h-5" />
                      <span>Update</span>
                    </>
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* View Modal */}
      {showViewModal && viewedKey && (
        <div className="fixed inset-0 bg-gray-900 bg-opacity-75 z-50 flex items-center justify-center p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-md w-full">
            <div className="px-6 py-4 border-b flex items-center justify-between">
              <h3 className="text-xl font-bold text-gray-900">API Key Details</h3>
              <button
                onClick={() => {
                  setShowViewModal(false);
                  setViewedKey(null);
                  setShowFullKey(false);
                }}
                className="text-gray-500 hover:text-gray-700"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Key ID
                </label>
                <div className="flex items-center space-x-2">
                  <input
                    type="text"
                    value={viewedKey.id}
                    readOnly
                    className="flex-1 px-4 py-2 bg-gray-50 border border-gray-300 rounded-lg"
                  />
                  <button
                    onClick={() => copyToClipboard(viewedKey.id)}
                    className="p-2 text-gray-600 hover:text-gray-900"
                  >
                    {copied ? <Check className="w-5 h-5 text-green-600" /> : <Copy className="w-5 h-5" />}
                  </button>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  API Key (Masked)
                </label>
                <div className="flex items-center space-x-2">
                  <input
                    type="text"
                    value={viewedKey.key}
                    readOnly
                    className="flex-1 px-4 py-2 bg-gray-50 border border-gray-300 rounded-lg font-mono text-sm"
                  />
                  <button
                    onClick={() => copyToClipboard(viewedKey.key)}
                    className="p-2 text-gray-600 hover:text-gray-900"
                  >
                    {copied ? <Check className="w-5 h-5 text-green-600" /> : <Copy className="w-5 h-5" />}
                  </button>
                </div>
                <p className="text-xs text-gray-500 mt-1">
                  First 8 characters + ... + Last 4 characters
                </p>
              </div>

              <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-3">
                <p className="text-sm text-yellow-800">
                  <strong>Security Note:</strong> For security reasons, only a masked 
                  version of the key is displayed. Store the full key securely.
                </p>
              </div>

              <div className="flex justify-end pt-4">
                <button
                  onClick={() => {
                    setShowViewModal(false);
                    setViewedKey(null);
                    setShowFullKey(false);
                  }}
                  className="px-6 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}