"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { toast } from "react-toastify";
import { adminService } from "../../services/adminService";
import { 
  Users, 
  LogOut, 
  Loader2,
  Shield,
  Globe,
  Key,
  DollarSign,
} from "lucide-react";

// Import section components
import UserManagement from "./UserManagement";
import PlanetPredictionsManagement from "./PlanetPredictionsManagement";
import ApiKeysManagement from "./ApiKeysManagement";
import IncomeAnalytics from "./IncomeAnalytics";

type TabType = "users" | "predictions" | "apikeys" | "analytics";

export default function AdminDashboard() {
  const router = useRouter();
  
  const [admin, setAdmin] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState<TabType>("users");

  useEffect(() => {
    checkAuth();
  }, []);

  const checkAuth = async () => {
    try {
      const result = await adminService.getProfile();
      if (result.success) {
        setAdmin(result.admin);
      } else {
        router.push("/admin");
      }
    } catch (error) {
      router.push("/admin");
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = async () => {
    try {
      await adminService.logout();
      toast.info("Logged out successfully");
      router.push("/admin");
    } catch (error) {
      toast.error("Logout failed");
    }
  };

  if (loading && !admin) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Loader2 className="w-8 h-8 animate-spin text-indigo-600" />
      </div>
    );
  }

  const tabs = [
    { id: "users" as TabType, label: "User Management", icon: Users },
    { id: "predictions" as TabType, label: "Planet Predictions", icon: Globe },
    { id: "apikeys" as TabType, label: "API Keys", icon: Key },
    { id: "analytics" as TabType, label: "Income Analytics", icon: DollarSign },
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <Shield className="w-8 h-8 text-indigo-600" />
              <div>
                <h1 className="text-2xl font-bold text-gray-900">Admin Dashboard</h1>
                <p className="text-sm text-gray-600">Welcome, {admin?.name}</p>
              </div>
            </div>
            <button
              onClick={handleLogout}
              className="flex items-center space-x-2 px-4 py-2 bg-red-50 text-red-600 rounded-lg hover:bg-red-100 transition"
            >
              <LogOut className="w-4 h-4" />
              <span>Logout</span>
            </button>
          </div>
        </div>
      </header>

      {/* Navigation Tabs */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <nav className="flex space-x-8" aria-label="Tabs">
            {tabs.map((tab) => {
              const Icon = tab.icon;
              return (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id)}
                  className={`
                    flex items-center space-x-2 py-4 px-1 border-b-2 font-medium text-sm transition
                    ${activeTab === tab.id
                      ? "border-indigo-500 text-indigo-600"
                      : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                    }
                  `}
                >
                  <Icon className="w-5 h-5" />
                  <span>{tab.label}</span>
                </button>
              );
            })}
          </nav>
        </div>
      </div>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {activeTab === "users" && <UserManagement />}
        {activeTab === "predictions" && <PlanetPredictionsManagement />}
        {activeTab === "apikeys" && <ApiKeysManagement />}
        {activeTab === "analytics" && <IncomeAnalytics />}
      </main>
    </div>
  );
}