"use client";

import { useState, useEffect } from "react";
import { adminService } from "../../services/adminService";
import { toast } from "react-toastify";
import { DollarSign, TrendingUp } from "lucide-react";

export default function IncomeAnalytics() {
  const [analytics, setAnalytics] = useState({
    dailyIncome: 0,
    monthlyIncome: 0,
    yearlyIncome: 0,
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchAnalytics();
  }, []);

  const fetchAnalytics = async () => {
    try {
      setLoading(true);
      const result = await adminService.getIncomeAnalytics();
      if (result.success) {
        setAnalytics(result.analytics);
      } else {
        toast.error("Failed to load analytics");
      }
    } catch (error) {
      toast.error("Failed to load analytics");
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <div className="text-center py-8">Loading...</div>;
  }

  return (
    <div className="grid md:grid-cols-3 gap-6">
      <div className="bg-white rounded-xl shadow-md p-6">
        <div className="flex items-center space-x-3 mb-2">
          <DollarSign className="w-6 h-6 text-green-500" />
          <h3 className="font-semibold text-gray-900">Daily Income</h3>
        </div>
        <p className="text-3xl font-bold text-gray-900">LKR {analytics.dailyIncome.toFixed(2)}</p>
      </div>
      <div className="bg-white rounded-xl shadow-md p-6">
        <div className="flex items-center space-x-3 mb-2">
          <DollarSign className="w-6 h-6 text-blue-500" />
          <h3 className="font-semibold text-gray-900">Monthly Income</h3>
        </div>
        <p className="text-3xl font-bold text-gray-900">LKR {analytics.monthlyIncome.toFixed(2)}</p>
      </div>
      <div className="bg-white rounded-xl shadow-md p-6">
        <div className="flex items-center space-x-3 mb-2">
          <TrendingUp className="w-6 h-6 text-purple-500" />
          <h3 className="font-semibold text-gray-900">Yearly Income</h3>
        </div>
        <p className="text-3xl font-bold text-gray-900">LKR {analytics.yearlyIncome.toFixed(2)}</p>
      </div>
    </div>
  );
}